.main-container {
  width: fit-content;
  height: 602px;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  border: 1px solid #dbdbdb;
}

.media {
  width: 600px;
  height: 600px;
  object-fit: cover;
}

.post-details {
  padding: 20px 0;
  width: 350px;
  border-left: 1px solid #dbdbdb;
}

.header-border {
  border-bottom: 1px solid #efefef;
  padding-bottom: 20px;
}

.header {
  display: flex;
  padding: 0 15px;
  align-items: center;
}

.caption {
  display: flex;
  margin: 20px 0 30px 0;
  padding: 0 15px;
  align-items: flex-start;
}

.comment {
  display: flex;
  margin: 20px 0;
  padding: 0 15px;
  align-items: flex-start;
}

.body {
  display: flex;
  flex-direction: column;
  margin-left: 15px;
}

.user-image {
  width: 32px;
  height: 32px;
  border-radius: 50%;
}

.user-name {
  display: inline-block;
  font-size: 0.9rem;
  color: #262626;
  font-weight: 600;
  margin-right: 5px;
}

.text {
  font-size: 0.9rem;
  color: #262626;
  font-weight: 400;
}

.footer {
  margin-top: 10px;
}

.date {
  font-size: 0.8rem;
  color: #8e8e8e;
  font-weight: 400;
}
